<template>
  <div class="dialog" v-show="listStatus">
    <div class="dialog-container">
      <div class="dialog-title">
        <div class="close">
          <img src="../../../images/close@2x.png" alt="" @click="goBack()" />
        </div>
        <span v-if="listType">{{language.myBackpack}}</span>
        <span v-else> {{language.pledged}}NFT </span>
      </div>


      <div class="knapsack">
        <div
          v-for="(item, index) in tranches"
          :key="index"
          @click="getDefineFn(index, item.trancheID)"
        >
          <img :src="item.resUrl" alt="" class="img1" />
          <img
            src="../../../images/confirm.png"
            alt=""
            class="img2"
            v-if="statusIndex == index"
          />
          <div class="watch">
            <b>#{{ item.trancheID }}</b>
            <b>￥{{ item.amount }}</b>
          </div>
        </div>
      </div>

      <div class="chooseGuard" v-if="listType">
        *  {{language.calculating}}
      </div>
      <div class="everyday" v-if="listType">* {{language.takenAny}}</div>

      <div class="chooseGuard" v-if="!listType">* {{language.stopProducing}}</div>
      <div class="everyday" v-if="!listType">*{{language.willDestroyed}}</div>

      <div class="preservation" @click="closeFn()">
        <m-button class="hdr-el-blue" :height="36" style="width: 148px" v-if="listType">
           {{language.determiningPledge}}
        </m-button>
        <m-button class="hdr-el-blue" :height="36" style="width: 148px" v-else>
           {{language.takeOut}}
        </m-button>
      </div>
    </div>

    <!--<ul>
      <li v-if="myCardsFiltered.length===0&&!bagLoading" class="no-data flex jc-center ai-center">
          <p class="text fs-16 ta-center">
            <img src="../../../images/no-data.png" alt="nodata"><br>
            {{language.nodata}} NFT
          </p>
        </li>
        <li v-if="bagLoading" class="mask flex jc-center ai-center">
          <i class="icon-loading"></i>
      </li>
    </ul> -->
  </div>
</template>

<script>
import MButton from "@/components/mButton";
import {mapState,mapGetters} from "vuex";
export default {
  components: { MButton },
  props: {
    listStatus: {
      type: Boolean,
      default: false,
    },
    listType:{
      type: Boolean,
      default: false,
    },
   
  },
  computed: {
    ...mapState('app',['address']),
    ...mapState('nft', ['myCards']),
    ...mapGetters(['language']),
     myCardsFiltered(){
        return this.myCards.filter(c=>c.id.indexOf(this.numText)!==-1)
    },
  },
   watch:{
    address(){
      this.getCardList();
    },
   
  },
  data() {
    return {
       numText:'',
      list: 31,
      statusIndex:"",
      id:"",
      url:"",
      tranches:[],
    };
  },
  mounted() {

  },
  
  methods: {
  // +
    initQuery(lpToken){
        this.tranches = [];
        this.$api.getDefxCallHistoryList(this.address,1,lpToken).then(({tranches}) => {
        this.tranches =  tranches;
      }).catch(() => {
        this.loading = false;
      })
    },
    //  减的方法
    initQueryReduce(indexStatus){
       this.tranches = [];
      this.$api.queryStaking(indexStatus).then((res) => {
         this.tranches =  res.optionPoolRecords;
      }).catch(() => {
         this.loading = false;
      })
    },
    // 确定
    closeFn() {
      if(this.id){
        this.$emit("pledgeFn",this.id);
      }else{
           this.$emit("pledgeFn")
      }
    },
    // 返回
    goBack(){
         this.$emit("pledgeFn");
    },
    // 选择nft
    getDefineFn(index,id){
          this.statusIndex = index;
          this.id = id;
    },
  },
};
</script>
