<script>
// mapActions
import MTip from "@/components/mTip";
import { mapGetters,mapState } from "vuex";
import pledgeList from "../pledgeFarm/Popup/pledgeList";
import MButton from "@/components/mButton";
import {blockConfig} from "@/common/walletConfig";
export default {
  name: "farm",
  components: {  MButton , pledgeList,MTip},
  computed: {
    ...mapGetters(["language"]),
    ...mapState('app', ['address']),

    
  },
  data() {
    return {
      listStatus:false,
      statusStype: false,
      filterFlag: false,
      searchText: "",
      poolLength: 0,
      loading: false,
      tranches:[],
      listLenght:[],
      userInfoList: [],
      isApproved:false,
      listType:1,
      indexStatus:"",
    }
  },
  created(){
    
      // 查询列表长度
      this.initQuery();
       
  },
  methods: {
       
    async initQuery(){
      var len = await this.$api.poolLength();
      this.userInfoList = [];
      this.listLenght =  [] ;
      var list = await this.$api.getOptionPools(len);
      for (let i = 0; i < list.length; i++) {
        if(list[i].status  != 1){
           continue
        }
        var data = await this.$api.getQueryEarn(i)
        let item = {}
        item = Object.assign({...data}, list[i]);
        item.id = i;

       let arrName =  item.name.toLocaleUpperCase().split("_")
       item.name = arrName[1];
       item.symbol = arrName[0];
       item.isApproved = await this.$api.checkIsAmNFTApproved(item.lpToken);
        
         

        this.listLenght.push(item)
      
        this.annualizedRate(item)
        console.log(item)
      }
      //this.listLenght  = list
    },
       // 年化率
      annualizedRate(itme){
        let {token0symbol}  = itme;
        this.$api.getPriceByOracle(token0symbol.toLocaleUpperCase()).then(async (res)=>{
          let totalAmount = res * itme.amount;
          if(totalAmount == 0) {
            this.listLenght[itme.id].annualizedRate = "0";
            return;
          }
          let dcoinAddres =  this.$api.getContractAddress("ERC20", "DCoin");
          let dcoinPrice =0;
          try {
              dcoinPrice = await this.$api.getTokenPrice(dcoinAddres, 18);
          }catch(e) {
             dcoinPrice = 1;
          }
          let detTokenPerBlock = await this.$api.getOptionPoolDetTokenPerBlock();
          let dcoinTotalAmount = detTokenPerBlock * dcoinPrice;
          this.listLenght[itme.id].annualizedRate = dcoinTotalAmount * 100 / totalAmount;
        })
     },

  
    showPopper(type,index,lpToken){
        this.listType = type
        this.listStatus = true;
        this.indexStatus = index
      //  +
       if(type){
          this.$refs.pledgeListRef.initQuery(lpToken);
       }else{
        //  -
         this.$refs.pledgeListRef.initQueryReduce(index);
       }
       

    },
    pledgeFn(id){
      if(id){
          // 加仓
          if(this.listType){
            this.stakingFnPlus(id);
          }else{
            this.stakingFnReduce(id)
          }
      }else{
            this.listStatus = false;
      }
    },
   stakingFnPlus(id){
        this.$api.staking(id,this.indexStatus).then(()=>{
            this.listStatus = false;
            this.initQuery();
        })
   },
   stakingFnReduce(id){
        this.$api.unstaking(id,this.indexStatus).then(() =>{
           this.listStatus = false;
           this.initQuery();
        })
   },
  //  收割
    harvestFn(id){
          this.reapLoading = true
          this.$api.harvestFnNft(id).then(()=>{
            this.reapLoading = false
            this.initQuery();
          }).catch(()=>{
              this.reapLoading = false
        })     
    },
    // 授权
    setApproved(lpToken) {
      this.loading = true;
      this.$api.setAmNFTApprovalForAll(lpToken).then(() => {
        this.loading = false;
        this.isApproved = true;
        this.initQuery();
      }).catch(() => {
        this.loading = false;
      })
    },
    toPoolLink(lpToken) {
      window.open(blockConfig.poolHost + lpToken);
    },
  },
  beforeMount() {
     
  },
};
</script>

